import { Component, ElementRef } from '@angular/core';
import { FileUploadService } from '../../services/file-upload.service';
import { HttpErrorResponse } from '@angular/common/http';

@Component({
    selector: 'app-upload-file',
    templateUrl: './upload-file.component.html',
    styleUrls: ['./upload-file.component.css']
})

export class UploadFileComponent {

    constructor( private _el: ElementRef, private fileToUpload: FileUploadService ) { }

    errorMsg: any;

    uploadFile() {
        const frmData = new FormData(this._el.nativeElement.querySelector('form'));
        this.fileToUpload.uploadFile(frmData)
        .subscribe(
            data => console.log(data),
            (err: HttpErrorResponse) => {
                console.log(err.error.text);
                this.errorMsg = err.error.text;
            }
        );
    }

}
