import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { RouterModule, Routes } from '@angular/router';

import { AppComponent } from './app.component';
import { FeaturesComponent } from './components/features/features.component';
import { PricingComponent } from './components/pricing/pricing.component';
import { SidebarComponent } from './components/sidebar/sidebar.component';
import { PostsComponent } from './components/posts/posts.component';
import { PostComponent } from './components/post/post.component';
import { LoginComponent } from './components/login/login.component';

import { FetchdataService } from './services/fetchdata.service';
import { PostsdataService } from './services/postsdata.service';
import { HighLighterDirective } from './directives/high-lighter.directive';
import { MyNgIfDirective } from './directives/my-ng-if.directive';
import { CapitalizePipe } from './capitalize.pipe';

import { HttpClientModule } from '@angular/common/http';
import { FileUploadService } from './services/file-upload.service';
import { UploadFileComponent } from './components/upload-file/upload-file.component';

let routes: Routes = [
    {
        path: "features",
        component: FeaturesComponent
    },
    {
        path: "pricing",
        component: PricingComponent
    },
    {
        path: "posts",
        component: PostsComponent
    },
    {
        path: "posts/:id",
        component: PostComponent
    },
    {
        path: "upload",
        component: UploadFileComponent
    },
    {
        path: "login",
        component: LoginComponent
    }
]

@NgModule({
  declarations: [
    AppComponent,
    FeaturesComponent,
    PricingComponent,
    SidebarComponent,
    PostsComponent,
    PostComponent,
    LoginComponent,
    HighLighterDirective,
    MyNgIfDirective,
    CapitalizePipe,
    UploadFileComponent,
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpClientModule,
    RouterModule.forRoot(routes, { useHash: true }),
  ],
  providers: [FetchdataService, PostsdataService, FileUploadService],
  bootstrap: [AppComponent]
})

export class AppModule { }