import { Component, OnInit } from '@angular/core';
import { PostsdataService } from '../../services/postsdata.service';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-posts',
  templateUrl: './posts.component.html',
  styleUrls: ['./posts.component.css']
})

export class PostsComponent implements OnInit {

    posts: any;

    constructor( private _postData: PostsdataService, private _http: HttpClient ) {}

    ngOnInit() {
        this.posts = this._postData._data;
    }

    sendData() {
        this._http.post('https://jsonplaceholder.typicode.com/posts', {
            'title': 'This is test swapnil title',
            'body': 'This is test swapnil body'
        })
        .subscribe(
            result => console.log(result),
            err => console.log(err),
            () => console.log('data save success...')
        );
    }

}
